/* oxygen-300normal - latin */
@font-face {
  font-family: 'Oxygen';
  font-style: normal;
  font-display: swap;
  font-weight: 300;
  src:
    local('Oxygen Light '),
    local('Oxygen-Light'),
    url('./files/oxygen-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/oxygen-latin-300.woff') format('woff'); /* Modern Browsers */
}

